.homePageDiv{height:100%}.homePageDiv .homePageBody{padding-top:75px;height:100%}@media screen and (max-width: 599px){.homePageDiv .homePageBody{padding-top:65px}}.homePageDiv h2{margin-top:0;margin-bottom:0}.homePageDiv ul{list-style:none;padding-left:0;height:100%;display:grid;grid-template-columns:1fr 1fr}.homePageDiv ul li{border:black solid 1px;color:white;transition:ease .5s;display:flex;align-items:center;justify-content:center}.homePageDiv ul li:hover{cursor:pointer;opacity:.8;background-size:cover;background-blend-mode:multiply}.homePageDiv ul li span{display:block;font-family:'Dancing Script', cursive;font-size:22px;font-weight:600}.homePageDiv ul .topicGreeting{grid-column:span 2;background:url(/redux-exercise/static/media/greeting.aa9f56a0.jpg) rgba(130,130,130,0.5);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.homePageDiv ul .topicFood{grid-row:span 2;background:url(/redux-exercise/static/media/food.6c05ee0a.jpg) rgba(130,130,130,0.5);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.homePageDiv ul .topicMovie{background:url(/redux-exercise/static/media/movie.71dd3035.jpg) rgba(130,130,130,0.5);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.homePageDiv ul .topicProgramming{grid-row:span 2;background:url(/redux-exercise/static/media/programming.6d53aac9.jpg) rgba(130,130,130,0.5);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.homePageDiv ul .topicOutdoor{background:url(/redux-exercise/static/media/outdoor.65abef21.jpg) rgba(130,130,130,0.5);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.homePageDiv ul .topicTraveling{grid-row:span 2;background:url(/redux-exercise/static/media/traveling.9f9cb6d1.jpg) rgba(130,130,130,0.5);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.homePageDiv ul .topicSchool{grid-row:span 2;background:url(/redux-exercise/static/media/school.d2f2cd68.jpg) rgba(130,130,130,0.5);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}

.Chat{text-align:center;height:100vh}.pageStyle{height:100%;display:grid;grid-template-columns:1fr;grid-template-rows:2fr .7fr}

.commentListWrapper{margin-top:56px;padding:2rem;overflow:scroll}@media screen and (max-width: 599px){.commentListWrapper{margin-top:48px}}.commentListWrapper .commentList{list-style-type:none;padding-left:0;margin:auto;max-width:500px;text-align:left}.commentListWrapper .commentList li{padding:.25rem;margin-bottom:.8rem;background-color:white;border-radius:10px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.4),0 6px 20px 0 rgba(0,0,0,0.05)}.commentListWrapper .commentList li p{margin:.5rem;line-height:1.5}.commentListWrapper .commentList li .commentData{font-size:.75rem}.commentListWrapper .commentList li .commentData *{margin-right:.5rem}.greetingBackGround{background:url(/redux-exercise/static/media/greeting.aa9f56a0.jpg) rgba(130,130,130,0.8);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.foodBackGround{background:url(/redux-exercise/static/media/food.6c05ee0a.jpg) rgba(130,130,130,0.8);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.movieBackGround{background:url(/redux-exercise/static/media/movie.71dd3035.jpg) rgba(130,130,130,0.8);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.programmingBackGround{background:url(/redux-exercise/static/media/programming.6d53aac9.jpg) rgba(130,130,130,0.8);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.outdoorBackGround{background:url(/redux-exercise/static/media/outdoor.65abef21.jpg) rgba(130,130,130,0.8);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.travelingBackGround{background:url(/redux-exercise/static/media/traveling.9f9cb6d1.jpg) rgba(130,130,130,0.8);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}.schoolBackGround{background:url(/redux-exercise/static/media/school.d2f2cd68.jpg) rgba(130,130,130,0.8);background-size:cover;background-blend-mode:multiply;background-repeat:no-repeat}

.inputAreaDiv{width:100%;box-shadow:0 4px 8px 0 rgba(0,0,0,0.4),0 6px 20px 0 rgba(0,0,0,0.19)}.inputAreaDiv form{max-width:350px;margin:1rem auto;padding:1rem}.inputAreaDiv form input{margin:0 auto;margin-bottom:.5rem;font-size:16px;padding:.2rem;display:block;width:80%}.inputAreaDiv form textarea{margin:0 auto;height:80px;margin-bottom:.5rem;font-size:16px;padding:.2rem;display:block;width:80%}.inputAreaDiv form button{margin:0 auto;width:82%}

